@import url(http://fonts.googleapis.com/css?family=PT+Sans:400,700,400italic&subset=latin,cyrillic);
body, h1, h2, h3, h4, h5, h6, p, a {
  font-family: "PT Sans", "Open Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif; }

.header {
  text-align: center;
  background: url("../img/header-xs.jpg") no-repeat center center;
  background-size: cover;
  height: 100vh; }
  @media (min-width: 480px) {
    .header {
      background: url("../img/header-sm.jpg") no-repeat center center;
      background-size: cover; } }
  @media (min-width: 768px) {
    .header {
      background: url("../img/header-lg.jpg") no-repeat center center;
      background-size: cover; } }
  .header img {
    padding-top: 2em; }
  .header p {
    font-size: 2em;
    margin-top: 1em; }
    @media (max-width: 767px) {
      .header p {
        background: rgba(59, 39, 30, 0.4);
        color: #FFFFFF; } }
  .header .cont-h {
    height: calc(100vh - 172px); }
  .header table {
    height: 100%;
    width: 100%; }
  .header td {
    text-align: center;
    vertical-align: top; }
    @media (max-width: 767px) {
      .header td {
        vertical-align: bottom; } }

.content {
  text-align: center;
  padding: 20px 0 20px 0; }
  @media (min-width: 480px) {
    .content {
      padding: 30px 0 30px 0; } }
  @media (min-width: 768px) {
    .content {
      padding: 40px 0 40px 0; } }
  .content h2 {
    font-size: 3.5em;
    text-transform: uppercase; }
  .content .lead {
    font-size: 2em; }
  .content p {
    font-size: 1.5em; }
  .content.welcome {
    background: #f0ddaa; }
  .content.mission {
    background: #f0ddaa;
    padding: 0; }
    .content.mission h2 {
      font-size: 3em;
      text-transform: none;
      margin-top: 10px; }
    .content.mission .mission-border {
      border-top: solid #413e4a 3px;
      padding: 20px 0 20px 0; }
      @media (min-width: 480px) {
        .content.mission .mission-border {
          padding: 30px 0 30px 0; } }
      @media (min-width: 768px) {
        .content.mission .mission-border {
          padding: 40px 0 40px 0; } }
  .content.history {
    padding: 0; }
  .content .maincont {
    text-indent: 1.5em;
    text-align: justify;
    font-size: 1.5em;
    margin: 1.5em 0; }
  .content .history-img {
    height: 600px;
    background-image: url("../img/history.jpg");
    padding: 0; }
  .content .parent {
    height: 600px;
    display: table; }
  .content .history-text {
    display: table-cell;
    vertical-align: middle; }
  .content.quote {
    background: #f0ddaa; }
    .content.quote blockquote {
      text-align: left;
      border-color: #413e4a; }

.gallery {
  padding: 0; }
  .gallery img {
    width: 100%; }

.footer {
  color: #f9f9f9;
  background: #152b3c;
  text-align: center;
  padding: 30px 0 15px 0;
  a: visited;
    a-color: #f9f9f9; }
  @media (min-width: 480px) {
    .footer {
      padding: 45px 0 30px 0; } }
  @media (min-width: 768px) {
    .footer {
      padding: 60px 0 45px 0; } }
  .footer h3 {
    font-size: 2em;
    text-transform: uppercase; }
  .footer p {
    font-size: 1.5em; }
  .footer ul {
    margin-bottom: 0;
    padding-left: 0;
    background: #d9534f; }
  .footer ul li {
    display: inline;
    font-size: 3em;
    margin-right: 15px; }
    .footer ul li:nth-last-child(1) {
      margin-right: 0; }
  .footer a {
    color: #f9f9f9; }
    .footer a:focus, .footer a:hover {
      color: #152b3c; }
  @media (max-width: 767px) {
    .footer .btn {
      font-size: 1.5em; } }
  .footer .copyright {
    padding-top: 50px; }
  .footer .a-foot {
    color: #d9534f; }
    .footer .a-foot:focus, .footer .a-foot:hover {
      color: #c9302c; }
  .footer table {
    border: 2px solid white;
    width: 80%; }
